@media (520px <= width <= 630px) {
    .overlay-content1 h2 {
        font-size: 20px;
    }
}

@media (350px <= width <= 519px) {
    .overlay-content1 h2 {
        font-size: 17px;
    }
}
@media (929px <= width <= 1100px) {
    .overlay p {
        font-size: 12px;
    }
    .overlay h2 {
        font-size: 19px;
    }
}

